<template>
  <div>
    <div class="url-list">
      <div v-if="urls.length === 0">No URLs found in the file.</div>
      <div v-else>
        <div class="image-scroll-container">
          <div class="image-container">
            <!-- Iterate over the URLs and display each image -->
            <img
              v-for="(url, index) in urlsWithHttp"
              :key="index"
              :src="url"
              alt="image"
              class="image-item"
              @click="enlargeImage(url)"
            />
          </div>
        </div>
      </div>
    </div>

    <!-- Modal for displaying enlarged image -->
    <div v-if="enlargedImage" class="modal" @click="closeModal">
      <div class="modal-content">
        <span class="close" @click="closeModal">&times;</span>
        <img :src="enlargedImage" alt="Enlarged image" class="enlarged-image">
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'arrayImage',
  props: {
    projectPath: {
      type: String,
      required: true
    },
    urls: {
      type: Array,
      required: true
    }
  },
  data() {
    return {
      enlargedImage: null // Store the URL of the currently enlarged image
    };
  },
  computed: {
    urlsWithHttp() {
      // Add "https://res.cloudinary.com/alvin-tan/image/upload/" prefix to each URL
      return this.urls.map(url => "https://res.cloudinary.com/alvin-tan/image/upload/" + this.projectPath + "/" + url);
    }
  },
  methods: {
    enlargeImage(url) {
      this.enlargedImage = url; // Set the URL of the clicked image
    },
    closeModal() {
      this.enlargedImage = null; // Reset the enlarged image URL to close the modal
    }
  }
};
</script>

<style>
.url-list {
  margin-top: 20px;
}

.image-scroll-container {
  overflow-x: auto;
  white-space: nowrap;
}

.image-container {
  display: inline-block;
}

.image-item {
  width: auto;
  max-width: 100%;
  height: 300px; /* Set a fixed height for the images */
  margin: 5px;
  object-fit: contain;
  cursor: pointer; /* Add cursor pointer to indicate clickable image */
}

/* Modal styles */
.modal {
  display: flex;
  position: fixed;
  z-index: 999;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7); /* Semi-transparent black background */
  align-items: center;
  justify-content: center;
}

.modal-content {
  position: relative;
  background-color: white;
  padding: 20px;
  border-radius: 5px;
}

.close {
  position: absolute;
  top: 10px;
  right: 10px;
  cursor: pointer;
}

.enlarged-image {
  max-width: 100%;
  max-height: 100%;
}
</style>
