<template>
  <div class="survey-form">
    <h1>Others interest</h1>
    <form @submit.prevent="handleSubmit">
      <!-- Type of Property -->
      <div class="form-group">
        <label for="propertyType">Type of Property:</label>
        <select v-model="form.propertyType" id="propertyType" required>
          <option value="" disabled>Select Property Type</option>
          <option>Residential</option>
          <option>Commercial</option>
          <option>Industrial</option>
          <option>Land</option>
        </select>
      </div>

      <!-- Location -->
      <div class="form-group">
        <label for="location">Location:</label>
        <select v-model="form.location" id="location" required>
          <option value="" disabled>Select Location</option>
          <option v-for="state in malaysiaStates" :key="state">{{ state }}</option>
        </select>
      </div>

      <!-- Purpose -->
      <div class="form-group">
        <label for="purpose">Purpose:</label>
        <select v-model="form.purpose" id="purpose" required>
          <option value="" disabled>Select Purpose</option>
          <option>Investment</option>
          <option>For Stay</option>
        </select>
      </div>

      <!-- Property Structure (only for Residential) -->
      <div class="form-group" v-if="form.propertyType === 'Residential'">
        <label for="propertyStructure">Property Structure:</label>
        <select v-model="form.propertyStructure" id="propertyStructure" required>
          <option value="" disabled>Select Property Structure</option>
          <option>Landed</option>
          <option>High-Rise</option>
        </select>
      </div>

      <!-- Budget -->
      <div class="form-group">
        <label for="budget">Budget:</label>
        <input type="number" v-model="form.budget" id="budget" placeholder="Enter Budget" required />
      </div>

      <!-- Facilities Required (only for Residential) -->
      <div class="form-group" v-if="form.propertyType === 'Residential'">
        <label>Facilities Required:</label>
        <div class="checkbox-group">
          <div v-for="facility in facilities" :key="facility">
            <input type="checkbox" :id="facility" :value="facility" v-model="form.facilities" />
            <label :for="facility">{{ facility }}</label>
          </div>
        </div>
      </div>

      <!-- Property Status (only for Residential) -->
      <div class="form-group" v-if="form.propertyType === 'Residential'">
        <label for="propertyStatus">Property Status:</label>
        <select v-model="form.propertyStatus" id="propertyStatus" required>
          <option value="" disabled>Select Property Status</option>
          <option>Ready Made</option>
          <option>Newly Launched</option>
        </select>
      </div>

      <!-- Remark -->
      <div class="form-group">
        <label for="remark">Additional Information:</label>
        <textarea v-model="form.remark" id="remark" placeholder="Enter Remark"></textarea>
      </div>

      <!-- Properties for Sale -->
      <div class="form-group">
        <label>Do you have properties for sale?</label>
        <div>
          <input type="radio" id="yes" value="Yes" v-model="form.hasPropertiesForSale" required />
          <label for="yes">Yes</label>
        </div>
        <div>
          <input type="radio" id="no" value="No" v-model="form.hasPropertiesForSale" required />
          <label for="no">No</label>
        </div>
      </div>

      <!-- Submit Button -->
      <div class="form-group">
        <button type="submit">Submit</button>
      </div>
    </form>

    <!-- Form Data Preview -->
    <div v-if="submitted" class="form-data-preview">
      <h3>Form Submitted:</h3>
      <pre>{{ form }}</pre>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      form: {
        propertyType: '',
        location: '',
        purpose: '',
        propertyStructure: '',
        budget: null,
        facilities: [],
        propertyStatus: '',
        remark: '',
        hasPropertiesForSale: '',
      },
      facilities: [
        'Swimming Pool',
        'Gym',
        'Library',
        'Jogging Path',
        'Playground',
        'Parking',
        'Security',
      ],
      malaysiaStates: [
        'Johor',
        'Kedah',
        'Kelantan',
        'Kuala Lumpur',
        'Labuan',
        'Melaka',
        'Negeri Sembilan',
        'Pahang',
        'Perak',
        'Perlis',
        'Pulau Pinang',
        'Putrajaya',
        'Sabah',
        'Sarawak',
        'Selangor',
        'Terengganu'
      ],
      submitted: false,
    };
  },
  methods: {
    handleSubmit() {
      this.submitted = true;
      // Further form submission logic (e.g., send data to server) can be added here
      console.log("Form submitted:", this.form);
    },
  },
};
</script>

<style scoped>
.survey-form {
  max-width: 600px;
  margin: 0 auto;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 8px;
  background-color: #f9f9f9;
}

.form-group {
  margin-bottom: 15px;
}

.form-group label {
display: block;
margin-bottom: 5px;
}

.form-group input,
.form-group select,
.form-group textarea {
width: 100%;
padding: 8px;
margin-top: 5px;
border: 1px solid #ccc;
border-radius: 4px;
}

.checkbox-group {
display: flex;
flex-wrap: wrap;
}

.checkbox-group div {
margin-right: 20px;
}

.checkbox-group input {
margin-right: 5px;
}

.checkbox-group label {
margin-right: 10px;
}

.form-group button {
padding: 10px 20px;
border: none;
border-radius: 4px;
background-color: #007bff;
color: white;
cursor: pointer;
}

.form-group button
{
background-color: #0056b3;
}

.form-data-preview {
margin-top: 20px;
padding: 10px;
border: 1px solid #ccc;
border-radius: 8px;
background-color: #e9e9e9;
}
</style>
